 .table-first {

   /* background-color: gray; */
   table {
     border-collapse: collapse;
     border: 1px solid gray;
     width: 100%;

   }

   td {
     border: 1px solid rgb(137 131 131);
     font-size: 20px;
     font-family: 'serif';
     font-weight: 500;
     height: 23px;
   }


   td:nth-child(4),
   td:nth-child(5),
   td:nth-child(6),
   td:nth-child(7),
   td:nth-child(8) {
     text-align: right;
   }

   tr {
     height: 23px;
   }
 }

